* {
  box-sizing: border-box
}
.App {
  position: relative;
  height: 100vh;
  text-align: center;
}
.liveBgBox {
  z-index: -2;
  position: absolute;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  transition: opacity 1s, transform .25s, filter .25s;
}
.bgbox{
  display: none;
  opacity: 0;
  z-index: -3;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 1s, transform .25s, filter .25s;
  backface-visibility: hidden;
}
.cover {
  z-index: -1;
  opacity: 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(rgba(0,0,0,0) 0%, rgba(0,0,0,0.5) 100%), radial-gradient(rgba(0,0,0,0) 33%, rgba(0,0,0,0.3) 166%);
  transition: .25s;
}
.Time {
  position: fixed;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-weight: inherit;
  text-shadow: 0 0 20px rgb(0 0 0 / 35%);
  cursor: pointer;
  transition: .25s;
  animation-name: delayedFadeIn;
  animation-duration: 2s;
  animation-iteration-count: 1;
  animation-delay: 0s;
}
.Search {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  width: 20%;
  max-width: 80%;
  outline: none;
  border: none;
  padding: 13px 15px;
  border-radius: 30px;
  color: rgba(255,255,255,0.8);
  font-size: small;
  font-weight: normal;
  background-color: rgba(255,255,255,.25);
  box-shadow: rgb(0 0 0 / 20%) 0 0 10px;
  backdrop-filter: blur(10px);
  transition: color 0.25s, background-color 0.25s, box-shadow 0.25s, left 0.25s, opacity 0.25s, top 0.25s, width 0.25s;
  text-align: center;
}
.Search:hover {
  width: 80%;
}
.Search::placeholder {
  color: white;
}